import React from "react";
import classes from "./Pickle.module.css";
import PickleIngredient from "./PickleIngredient/PickleIngredient";

const pickle = (props) => {
  let transformedIngredients = Object.keys(props.ingredients)
  .map(igKey => {
    return [...Array(props.ingredients[igKey])].map((_,i)=>{
      return <PickleIngredient key={igKey+i} type={igKey}/>
    })
  })
  .reduce((arr,el)=>{return arr.concat(el)},[])
  if(transformedIngredients.length === 0){
    transformedIngredients = <p>Please start adding ingredients!</p>
  }
  return (
    <div className={classes.Pickle}>
      <PickleIngredient type="bread-top"/>
      {transformedIngredients}
      <PickleIngredient type="bread-bottom"/>
    </div>
  )
}
export default pickle